
@charset "UTF-8";
.flex-grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #117ABD;
  height: 90%;
  position: relative;
  padding-right: 10px;
}
.col {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #fff;
  border-radius: 6px;
  margin-left: 10px;
  margin-bottom: 10px;
  padding: 10px;
  background: #eee;
  color: #000;
}
.col-title {
  font-weight: bold;
  font-size: 18px;
}
.nestedviewer {
  height: 100%;
  background: #117ABD;
}
.nestedviewer .item-box {
    border: 2px solid #ccc;
    margin-right: 3px;
    padding-left: 6px;
    padding-right: 6px;
    overflow: hidden;
    background: #fff;
}
.nestedviewer ul {
    list-style-type: none;
    padding: 0;
    padding-left: 0;
    line-height: 1.4em;
    list-style-type: none;
    margin-bottom: 8px;
}
.nestedviewer li {
    white-space: nowrap;
    margin-bottom: 4px;
    margin-top: 4px;
}
.nestedviewer li > div {
    padding-left: 0;
}
.nestedviewer .leaf-button {
    width: 10px;
    padding-left: 4px;
    height: 10px;
    display: inline-block;
}
.nestedviewer .leaf-button::after {
    content: '○';
}



